import React, { Component } from 'react';
import { w3cwebsocket as W3CWebSocket } from "websocket";
import PropTypes from 'prop-types';

// 'ws://ec2-18-206-120-187.compute-1.amazonaws.com:3000/'

class WebSocketService extends Component {
  static propTypes = {
    setClient: PropTypes.func.isRequired
  }
  state = {
    status: 'Pending...'
  }
  client = new W3CWebSocket('wss://echo.websocket.org/');
  componentDidMount() {
    this.client.onopen = () => {
      console.log('WebSocket Client Connected');
      this.setState({status: 'connected'})
    };
    this.client.onmessage = (message) => {
      console.log(message);
      this.setState({status: 'Message:'+message+' sent'})
    };
    this.props.setClient(this.client);
  }

  render() {
    return (
      <div>
        Connection status:
        {this.state.status}
      </div>
    );
  }
}

export default WebSocketService;